import AddOrg from "./components/AddOrg.vue";

import Url from "../../api";
import Utils from "@/utils/utils";

@Component({
  components: {
    AddOrg
  }
})
export default class Organization extends Vue {
  // 组织机构树
  treeMessage: any = {
    filterText: "",
    data: []
  };
  // 配置选项
  defaultProps: any = {
    value: "id",
    label: "name",
    children: "children"
  };

  basicForm: any = {
    id: "",
    name: "",
    typeDetailId: "",
    enterpriseFlag: "",
    extInfoVO: []
  };
  orgAccess = [];
  customOptions = [];
  baseTypeList: any = [{ id: "" }, { baseTypeName: "" }];

  parentId: any = null;
  currentId: any = null;
  type = "";
  visibleDialog = true;
  pageLoading: boolean = true;
  doLoading: boolean = true;
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("organization");

  testData: string = "800d19c845d03b9315a6063d43597522";

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.defaultProps.label].indexOf(value) !== -1;
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.orgTree as any).filter(val);
  }

  //获取组织机构树
  getTreeData() {
    this.pageLoading = true;
    this.$http.get(Url.getOrgTreeData).then((res: any) => {
      this.pageLoading = false;
      if (res.data.status) {
        this.treeMessage.data = res.data.data;
        let id = this.basicForm.id ? this.basicForm.id : this.treeMessage.data[0] ? this.treeMessage.data[0].id : "";
        if (id) this.getOrgInfo(id);
        this.doLoading = false;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //获取组织信息
  async getOrgInfo(orgId: any) {
    this.doLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.getOrgInfo + "/" + orgId);
      this.doLoading = false;
      if (status) {
        this.basicForm = data.orgInfo;
        this.basicForm.typeDetailId = this.basicForm.typeDetailId.split(",");
        this.basicForm.enterpriseFlag = this.basicForm.enterpriseFlag ? "是" : "否";
        this.customOptions = this.basicForm.extInfoVO ? this.basicForm.extInfoVO : [];
        // this.orgAccess = data.orgAccess ? data.orgAccess : []; // 避免报错
        // this.orgAccess.forEach((item: any) => {
        //   this.setDisabled(item.treeData);
        //   // 禁用已经配置的选项
        //   this.baseTypeList.some((temp: any) => {
        //     if (temp.id == item.typeId) {
        //       temp.disabled = true;
        //       return true;
        //     }
        //   });
        //   item.access = item.access != null ? item.access.split(",") : [];
        // });
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      this.doLoading = false;
      return this.$message.error("请求失败，请重试！");
    }
  }

  // 设置disabled属性
  setDisabled(arr: Array<any>) {
    for (let i in arr) {
      arr[i].disabled = true;
      if (arr[i].children && arr[i].children.length) {
        this.setDisabled(arr[i].children);
      }
    }
  }

  //获取基础属性类型列表
  getBaseTypeList() {
    this.$http.get(Url.getBaseTypeList).then((res: any) => {
      if (res.data.status) {
        this.baseTypeList = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //树节点点击事件
  orgNodeClick(data: any) {
    this.getOrgInfo(data.id); //获取选中的组织信息，填充到表单
  }

  //点击编辑组织机构信息
  editOrg(opt: any, data: any) {
    this.type = opt;
    switch (opt) {
      case "addTop":
        this.parentId = null;
        this.currentId = null;
        break;
      case "add":
        this.currentId = null;
        this.parentId = data.id;
        break;
      case "edit":
        this.currentId = data.id;
        break;
    }
    this.visibleDialog = false;
  }

  //删除组织机构
  remove(id: string) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(Url.delete + "/" + id).then((res: any) => {
        if (res.data.status) {
          this.$message.success(res.data.message);
          this.basicForm = {
            id: "",
            name: ""
          };
          this.getTreeData();
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: "error"
          });
        }
      });
    });
  }

  closeDialog() {
    this.visibleDialog = true;
    this.customOptions = [];
    this.getTreeData();
  }

  created() {
    // this.getBaseTypeList();
    this.getTreeData();
  }
}
