import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";
import Utils from "@/utils/utils";
import { ElTree } from "element-ui/types/tree";

@Component({
  components: {}
})
export default class AddOrg extends Vue {
  @Prop({ type: String, default: "add" })
  type?: string;

  @Prop({ type: [String, Object], default: null })
  id?: any;

  @Prop({ type: [String, Object], default: null })
  parentId?: any;

  // 配置选项
  defaultProps: any = {
    value: "id",
    label: "name",
    children: "children"
  };

  isLoading: boolean = false;

  dialogTitle = "";

  //单位属性下拉框
  enterOptions: any = [{ label: "否", value: 0 }, { label: "是", value: 1 }];
  // 组织机构类型树
  typeDetailOptions: Array<any> = [];
  orgVisible: boolean = false;

  addForm: any = {
    enterpriseFlag: 0
  };
  formRules: any = {
    name: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    code: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    typeDetailId: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "blur"
      }
    ]
  };

  hasCustomRole: boolean = qTouch.getBaseTypeByCode("organization");

  // 表单使用的组织信息
  // orgAccess = [{ typeId: null, treeData: [] }];

  // 自定义属性
  customOptions: Array<any> = [];

  //数据绑定basetype下拉框，基础属性类型列表
  baseTypeList: any = [{ id: "" }, { baseTypeName: "" }];

  closeDialog() {
    this.$emit("close");
  }

  //获取组织信息
  getOrgInfo(orgId: any) {
    this.isLoading = true;
    this.$http
      .get(Url.getOrgInfo + "/" + orgId)
      .then((response: any) => {
        if (response.data.status) {
          this.addForm = response.data.data.orgInfo;
          // this.addForm.enterpriseFlag = this.addForm.enterpriseFlag ? 0 : this.addForm.enterpriseFlag;
          // this.orgAccess = response.data.data.orgAccess;
          // this.orgAccess.forEach((item: any) => {
          //   // 禁用已经配置的选项
          //   this.baseTypeList.some((temp: any) => {
          //     if (temp.id == item.typeId) {
          //       temp.disabled = true;
          //       return true;
          //     }
          //   });
          //   item.access = item.access != null ? item.access.split(",") : [];
          // });
          this.customOptions = this.addForm.extInfoVO ? this.addForm.extInfoVO : [];
        } else {
          this.$message.error(response.data.message);
        }
      })
      .finally(() => {
        this.isLoading = false;
      });
  }

  // 组织类型
  getOrgType() {
    this.orgVisible = true;
    this.$http.get(Url.findTypeTree + "/organization").then((res: any) => {
      if (res.data.status) {
        this.typeDetailOptions = res.data.data;
        // this.$nextTick(() => {
        //   (this.$refs.orgTree as ElTree).setCheckedKeys([this.addForm.typeDetailId]);
        // });
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 类型树只允许单选
  handleOrgChange(data: any, checked: boolean) {
    if (checked) {
      (this.$refs.orgTree as any).setCheckedKeys([data.id]);
    }
  }

  // 确定类型
  saveOrg() {
    let typeDetailIds = (this.$refs.orgTree as any).getCheckedKeys();
    if (typeDetailIds.length) {
      this.addForm.typeDetailId = typeDetailIds[0];
    } else {
      this.addForm.typeDetailId = "";
    }
    this.orgVisible = false;
    if (this.addForm.typeDetailId) {
      this.changeCustomOptions();
    }
  }

  //获取基础属性类型列表
  getBaseTypeList() {
    this.$http.get(Url.getBaseTypeList).then((res: any) => {
      if (res.status) {
        this.baseTypeList = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //类型变化时重新渲染自定义信息
  changeCustomOptions() {
    this.$http.get(Url.getTemplateByTypeDetaiId + "/" + this.addForm.typeDetailId).then((res: any) => {
      if (res.data.status) {
        this.customOptions = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 获得组件CustomInput填充之后的数据
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["customInput"];
    if (customInput) {
      return await customInput.formValidation();
    }
    return null;
  }

  //保存组织机构基本信息
  saveOrgInfo() {
    //验证基本信息表单
    (this.$refs["addForm"] as any).validate((valid: any) => {
      if (valid) {
        //处理子组件自定义信息数据
        this.doSave();
      }
    });
  }

  //请求后台保存组织信息
  async doSave() {
    let url = this.type === "edit" ? Url.updateOrgBaseInfo : Url.addOrgBaseInfo;
    this.getFilledCustomOptions().then(data => {
      this.isLoading = true;
      let org = JSON.parse(JSON.stringify(Object.assign(this.addForm, { extInfo: data })));
      this.$http
        .post(url, org)
        .then((res: any) => {
          if (res.data.status) {
            this.$message.success("操作成功");
            this.addForm.id = res.data.data.id;
            this.closeDialog();
            // this.saveOrgAccessInfo(res.data.data.id); //保存权限信息
          } else {
            this.$message.error(res.data.message);
          }
        })
        .finally(() => {
          this.isLoading = false;
        });
    });
  }

  /**
