  //增加数据权限按钮
  // addAccess() {
  //   this.orgAccess.push({
  //     typeId: null,
  //     treeData: []
  //   });
  // }

  // 移除权限项
  // removeAccess(val: any) {
  //   Utils.removeArrItemByValue(this.orgAccess, val); //删除权限项
  //   this.baseTypeList.forEach((item: any) => {
  //     if (item.id == val.typeId) {
  //       item.disabled = false;
  //     }
  //   });
  // }

  created() {
    this.getBaseTypeList(); // 获取团队能见基础属性类型，用于编辑数据权限
    this.getOrgType();
    if (this.type === "edit") {
      this.dialogTitle = "编辑组织机构";
      this.getOrgInfo(this.id);
    } else {
      this.dialogTitle = "添加组织机构";
      if (this.type === "add") {
        this.addForm.parentId = this.parentId;
      }
    }
  }
}
